@charset "utf-8";

.todo_item {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 16px;
  line-height: 40px;
  border-bottom: 1px solid #c7eccc;
}
.todo_item:last-of-type{
  border-bottom: 0;
}

.todo_item:hover {
  background: #f4fff5
}

.todo_item:hover .remove {
  opacity: 1;
}

.remove {
  opacity: 0;
  margin-right: 0.5rem;
  font-size: 1rem;
}

.todo_text {
  flex: 1;
}

.checked {
  text-decoration: line-through;
  color: #00a030
}
.txt{
  position: relative;
}
.check_mark {
  position: absolute;
  right: 0; top: 10px;
  font-size: 1.3rem;
  line-height: 1rem;
}