@charset "utf-8";

.form{
  display: flex;
}
.form input{
  font-size: 1.25rem;
  border: none;
  outline: none;
  border-bottom: 1px solid #9ddfa5;
  /* 버튼의 가로너비를 제외한 나머지 영역을 차지한다 */
  /* flex: content; */
  flex: 1;
}
.create_btn{
  padding: 1rem 1.2rem;
  margin-left: 1rem;
  border-radius: 5px;
  border: none;
  background: #9ddfa5;
  font-size: 1.1rem;
  font-weight: bold;
  cursor: pointer;
}
.create_btn:hover{
  filter: brightness(1.1);
}