@charset "utf-8";
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&display=swap');

body{
  margin: 0; padding: 0;
  background: #f1f1f1;
  font-family: "noto sans kr";
}
.todo_list_template{
  width: 90%;
  background: #fff;
  margin: 4rem auto 0 auto;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 6px #999999;
  border: 3px solid #000;
}
.title{
  padding: 2rem;
  font-size: 2rem;
  text-align: center;
  font-weight: bold;
  background: #9ddfa5;
}
.form_wrapper{
  padding: 1rem;
  padding-bottom: 0;
  /* border-bottom: 1px solid #ddcbc4; */
}
.todos_wrappers{
  min-height: 5rem;
  /* padding-bottom: 16px; */
}